<template>
  <h1></h1>
</template>
<script>
  export default{
    mounted() {
      this.$nextTick(function () {
        this.$store.dispatch('login')
      })
    },
    computed: {
      isAutoLogin(){
        return this.$store.getters.isAutoLogin
      }
    },
  }
</script>



